<template>
  <v-card class="mx-auto overflow-hidden" height="auto">
    <v-app-bar
      xabsolute
      color="#363636"
      dark
      xshrink-on-scroll
      xdense
      xprominent
      xscroll-target="#scrolling-techniques-4"
    >
      <v-container>
        <v-row>
          <!-- RBS Logo -->
          <v-col sm="1">
            <div class="nav-logo mt-4 ml-1">
              <a href="/">
                <img
                  height="45"
                  width="45"
                  :src="fromAssetsFolder('roundabout_logo.png')"
                />
              </a>
            </div>
          </v-col>
          <!-- Top Navigation Icons+Label -->
          <v-col sm="10" class="d-flex justify-start pt-6">
            <router-link
              v-for="tab in navigation.tabs"
              @click.native="setActiveUrl(tab)"
              :to="tab.url"
              :key="tab.text"
              class="router-link-nav"
            >
              <div class="d-flex flex-column pl-6 pt-1">
                <v-icon>{{ tab.icon }}</v-icon>
                <p>{{ tab.text }}</p>
              </div>
            </router-link>
          </v-col>

          <!-- Settings Icon right -->
          <v-col sm="1" class="pt-6">
            <v-menu
              right
              dark
              :close-on-content-click="false"
              open-on-hover
              bottom
              offset-y
            >
              <template v-slot:activator="{ on, attrs }">
                <v-btn icon flat v-bind="attrs" v-on="on">
                  <v-icon x-large>mdi-account-cog</v-icon>
                </v-btn>
              </template>
              <v-list>
                <v-list-group
                  v-for="settingItem in navigation.settings"
                  :key="settingItem.text"
                  v-model="settingItem.active"
                  :prepend-icon="settingItem.action"
                  no-action
                  :class="settingItem.submenu ? 'hassubmenu' : 'nosubmenu'"
                  :to="true"
                  :value="true"
                >
                  <template v-slot:activator>
                    <v-list-item>
                      <v-list-item-content>
                        <v-list-item-title>{{
                          settingItem.text
                        }}</v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>
                  </template>

                  <!-- submenu items -->
                  <v-list-item
                    v-for="subItem in settingItem.submenu"
                    :key="subItem.text"
                    @click=""
                  >
                    <v-list-item-content>
                      <v-list-item-title>{{ subItem.text }}</v-list-item-title>
                    </v-list-item-content>

                    <v-list-item-action>
                      <v-icon>{{ subItem.action }}</v-icon>
                    </v-list-item-action>
                  </v-list-item>
                </v-list-group>
              </v-list>
            </v-menu>
          </v-col>
        </v-row>
      </v-container>
    </v-app-bar>
    <!-- Dev questions -->
    <h1>Dev questions</h1>
    <ul>
      <li>Hoe kan Settings > Account settings default opengeklapt worden?</li>
      <li>Submenu items hebben geen :hover state</li>
      <li>
        <a href="https://codepen.io/RoundaboutSocial/pen/vYxbYEN"
          >deze CodePen</a
        >
        heeft een v-toolbar drop-down menu; maar als ik de code overneem (onder)
        is de opmaak anders
      </li>
    </ul>
    <!-- v-toolbar code example -->
    <h3>v-toolbar</h3>
    <v-toolbar>
      <v-toolbar-title>Title</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn flat>Link One</v-btn>
        <v-btn flat>Link Two</v-btn>
        <v-btn flat>Link Three</v-btn>
        <v-menu :close-on-content-click="false" open-on-hover boattom offset-y>
          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark v-on="on"> Dropdown </v-btn>
          </template>

          <v-list>
            <v-list-group
              v-for="settingItem in navigation.settings"
              :key="settingItem.text"
              v-model="settingItem.active"
              :prepend-icon="settingItem.icon"
              no-action
            >
              <template v-slot:activator>
                <v-list-tile>
                  <v-list-tile-content>
                    <v-list-tile-title>{{
                      settingItem.text
                    }}</v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
              </template>

              <v-list-tile
                v-for="subItem in settingItem.submenu"
                :key="subItem.text"
                @click=""
              >
                <v-list-tile-content>
                  <v-list-tile-title>{{ subItem.text }}</v-list-tile-title>
                </v-list-tile-content>

                <v-list-tile-action>
                  <v-icon>{{ subItem.icon }}</v-icon>
                </v-list-tile-action>
              </v-list-tile>
            </v-list-group>
          </v-list>
        </v-menu>
      </v-toolbar-items>
    </v-toolbar>

    <!-- design screenshot -->
    <h3>
      Design:
      <img src="https://i.imgur.com/GE0u649.png" style="max-height: 300px" />
    </h3>
  </v-card>
</template>

<script>
import { mapActions, mapState } from "vuex";

import fromAssetsFolder2 from "./../utils";

export default {
  name: "Navigation",
  components: {},
  data() {
    return {
      // data structure van verticale navigatie aangepast
      navigation: {
        tabs: [
          {
            icon: "mdi-account-group-outline",
            text: "Community",
            url: "/navhor",
            active: false,
          },
          // {
          //   icon: "mdi-chat",
          //   text: "Chat",
          //   url: "/chat",
          //   active: false,
          // },
          {
            icon: "mdi-trending-up",
            text: "Your Campaigns",
            url: "/navhor",
            active: false,
          },
          {
            icon: "mdi-radar",
            text: "Your Audience trends",
            url: "/navhor",
            active: false,
          },
        ],
        settings: [
          {
            icon: "mdi-movie-outline",
            text: "Video Library",
            url: "/videolibrary",
            active: false,
          },
          {
            icon: "mdi-help-circle",
            text: "FAQ",
            url: "/faq",
            active: false,
          },
          {
            icon: "mdi-chevron-up",
            "icon-alt": "mdi-chevron-down",
            text: "Account settings",
            active: false,
            model: false,
            submenu: [
              {
                text: "Settings",
                url: "/account",
                icon: "mdi-tune",
                active: false,
              },
              {
                text: "Notification",
                url: "/notification/devices",
                icon: "mdi-bell-outline",
                active: false,
              },
              {
                text: "Social Media",
                url: "/account/settings/socialmedia",
                icon: "mdi-share-variant-outline",
                active: false,
              },
            ],
          },
        ],
      },
    };
  },
  computed: {
    ...mapState({
      totalUnseenChatMessages: "totalUnseenChatMessages",
    }),

    user: function () {
      return this.$store.state.user;
    },
  },
  methods: {
    fromAssetsFolder(img) {
      console.log(img);
      return "./static/" + img;
      return fromAssetsFolder2(img);
    },
    setActiveUrl(navigationTab) {
      window.alert("Naar â–º " + navigationTab.text);
    },
  },
  mounted() {
    //this.setActiveUrl();
  },
};
</script>
<style>
.nosubmenu .v-icon {
  /* instead of adding loads of v- if a menu item has no submenus, just hide the icon.. */
  display: none;
}
</style>

<style lang="scss" scoped>
.router-link-nav {
  --RBSlabelcolor: white;
  --RBShovercolor: #009788;
  text-decoration: none;
  color: var(--RBSlabelcolor);
}
// color both icon and label
a:hover i,
a:hover div {
  color: var(--RBShovercolor);
}
</style>
